.aboutMeContainer {
  width: 100%;
  display: inline-block;
  background: rgb(131,58,180);
  background: -moz-linear-gradient(133deg, rgba(131,58,180,1) 0%, rgba(45,114,204,0.9094012605042017) 76%, rgba(123,176,177,0.7161239495798319) 100%);
  background: -webkit-linear-gradient(133deg, rgba(131,58,180,1) 0%, rgba(45,114,204,0.9094012605042017) 76%, rgba(123,176,177,0.7161239495798319) 100%);
  background: linear-gradient(133deg, rgba(131,58,180,1) 0%, rgba(45,114,204,0.9094012605042017) 76%, rgba(123,176,177,0.7161239495798319) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#833ab4",endColorstr="#7bb0b1",GradientType=1);
  box-shadow: 0 8px 32px 0 rgba(76, 76, 77, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
}

.aboutMeNameTitle {
  line-height: 18px;
  padding: 20px;
}

.aboutMeNameTitle .name {
  text-transform: uppercase;
  font-size: 17px;
  font-weight: bold;
}
.aboutMeNameTitle .title {
  color: rgb(214, 213, 213);
  font-size: 13px;
}

.aboutmeIMG {
  margin-top: -40px;
  -webkit-clip-path: polygon(0 9%, 100 0%, 100% 94%, 0% 100%);
  clip-path: polygon(0 9%, 100% 0, 100% 94%, 0% 100%);
}

.aboutmeIMG img {
  width: 100%;
  /*black and white*/
  -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
  filter: grayscale(100%);
}

/* timeline About Me */

.timelineAboutme {
  top: 0;
  left: 0;
  margin: -40px 10px 30px;
}

.timelineAboutme .timelineContent {
  margin-top: 2px;
  margin-bottom: 10px;
}

.timelineText {
  font-size: 13.5px !important;
  color:rgb(214, 213, 213);
  font-weight: 400;
}
.timelineText > a {
  text-decoration: none;
}

.timelineText > span {
  color: rgb(0, 0, 0);
  font-weight: 400;
}

.aboutMeContainer .btnDlCvContainer {
  width: 100%;
  display: flex !important;
  justify-content: center;
  margin-top: 15px;
}

.aboutMeContainer .timelineAboutme {
  overflow: hidden;
  text-overflow: ellipsis;
}
.aboutMeContainer .timelineAboutme:hover {
  overflow: visible;
}